<template>
  <div>
    <div>
      <el-row class="pad-bt05 box v-c">
        <el-col :span="12">
          <router-link to="/phone">
            <el-button class="btn" type="danger"></el-button>
          </router-link>
        </el-col>
        <el-col :span="12" class="tx-r">

        </el-col>
      </el-row>
      <h3></h3>
      <div class="condition box v-c">
        <span class="c_name"></span>
        <el-radio-group v-model="filters.osCategory" @change="filter('osCategory',$event)">
          <el-radio-button label="iOS"></el-radio-button>
          <el-radio-button label="Android"></el-radio-button>
        </el-radio-group>
      </div>
      <!--<div class="condition box v-c">-->
        <!--<span class="c_name"></span>-->
        <!--<el-radio-group v-model="filters.osVersion">-->
          <!--<el-radio-button label="8.1"></el-radio-button>-->
          <!--<el-radio-button label="10"></el-radio-button>-->
        <!--</el-radio-group>-->
      <!--</div>-->
      <div class="condition box v-c">
        <span class="c_name"></span>
        <el-radio-group v-model="filters.location">
          <el-radio-button label=""></el-radio-button>
          <el-radio-button label=""></el-radio-button>
        </el-radio-group>
      </div>
      <div class="condition box v-c">
        <span class="c_name"></span>
        <el-radio-group v-model="filters.status">
          <el-radio-button label=""></el-radio-button>
          <el-radio-button label=""></el-radio-button>
        </el-radio-group>
      </div>
      <div class="mar-t1">
        <el-row>
          <el-col :span="12" class="tx-c">
            <router-link to="/phone">
              <el-button type="success"></el-button>
            </router-link>
          </el-col>
          <el-col :span="12" class="tx-c">
            <el-button type="warning" @click="clearFilter()"></el-button>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import {mapState, mapActions} from 'vuex'
  export default{
    data(){
      return {
      }
    },
    created(){
      //this.filters = this.$store.state.filters;
    },
    methods: {
      filter(key, val){
        this.conditionFilter({'condition': key, 'value': val});
      },
      clearFilter(){
        this.clearFilters();
      },
      ...mapActions([
        'conditionFilter',
        'clearFilters'
      ])
    },
    computed: mapState([
      'filters'
    ])
  }
</script>



