<template>
  <div>
    <el-row class="pad-bt05 box v-c">
      <el-col :span="24">
        <router-link to="./">
          <el-button class="btn" type="danger"></el-button>
        </router-link>
      </el-col>
    </el-row>
    <div v-for="(p,key) in phoneData" v-loading="loading"
         element-loading-text=",">
      <div class="device_main box v-c middle">
        <div class="device_img iphone7"></div>
        <h2 class="mar-t1">{{p.phoneVersion}}</h2>
        <span class="mar-t05">{{p.osCategory+' '+p.osVersion.substring(1)}}</span>
        <el-row class="box h-c mar-t05 pad-bt05">
          <el-col :span="14">
            <el-select v-model="p.owner" placeholder="" @change="changeOwner(p.owner)">
              <el-option v-for="item in options"
                         v-if="item.label!=p.holder"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-col>
        </el-row>
        <span class="mar-t05">{{p.location}}/{{p.state==1?"":""}}</span>
      </div>
      <div class="device_detail">
        <div class="bb-d tx-c">
          <h3></h3>
        </div>
        <el-row class="detail_item bb-d">
          <el-col :span="8"></el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05"></span>
            <span class="pad-b05 pad-t05"></span>
            <span class="pad-b05 pad-t05"></span>
            <span class="pad-b05 pad-t05"></span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.brand}}</span>
            <span class="pad-b05 pad-t05">{{p.phoneVersion}}</span>
            <span class="pad-b05 pad-t05">{{p.phoneVersionDetails}}</span>
            <span class="pad-b05 pad-t05">{{p.buyTime}}</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8"></el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05"></span>
            <span class="pad-b05 pad-t05"></span>
            <span class="pad-b05 pad-t05"></span>
            <span class="pad-b05 pad-t05"></span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.osCategory}}</span>
            <span class="pad-b05 pad-t05">{{p.osVersion.substring(1)}}</span>
            <span class="pad-b05 pad-t05">{{p.customizeOSName?p.customizeOSName:"--"}}</span>
            <span class="pad-b05 pad-t05">{{p.customizeOSVersion?p.customizeOSVersion:"--"}}</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8"></el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05"></span>
            <span class="pad-b05 pad-t05"></span>
            <span class="pad-b05 pad-t05">ppi</span>
            <span class="pad-b05 pad-t05">(G)</span>
            <span class="pad-t05"></span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.screenInches}}</span>
            <span class="pad-b05 pad-t05">{{p.screenResolution}}</span>
            <span class="pad-b05 pad-t05">326</span>
            <span class="pad-b05 pad-t05">{{p.ram}}</span>
            <span class="pad-t05">{{p.chargingPortType}}</span>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8"></el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05"></span>
            <span class="pad-b05 pad-t05"></span>
            <span class="pad-b05 pad-t05"></span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.owner}}</span>
            <span class="pad-b05 pad-t05"></span>
            <span class="pad-b05 pad-t05"></span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8"></el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05"></span>
            <span class="pad-b05 pad-t05"></span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.price}}</span>
            <span class="pad-b05 pad-t05">{{p.buyTime}}</span>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
  import {mapState} from 'vuex'
  import '../../assets/css/deatil.css'
  export default{
    data(){
      return {
        loading:false,
        pindex: this.$route.params.index,
        phoneData: [],
        options: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }]
      }
    },
    methods: {
      changeOwner(owner){
        this.loading=true;
        this.$http.jsonp("http://172.16.2.206:8080/QAdemoWebApp/phoneUpdateOwner.do", {
          //
          params:{
            uuid:this.phoneData[0]["uuid"],
            owner:owner,
          },
          jsonp: 'jsoncallback'
        }).then(function (response) {
          this.loading=false;
          this.open();
        },function (response) {
          this.loading=false;
        })
      },
      open() {
        this.$message({
          message:"",
          type: 'success',
          duration: 1000
        });
      }
    },
    created(){
      this.phoneData[0] = this.$store.device_list[this.pindex];
    }
  }
</script>



