import * as types from './mutation-types'

export const incrementCount = ({commit}, num) => {
  commit(types.INCREMENT_COUNT, num)
}

export const conditionFilter = ({commit}, payload) => {
  commit(types.CONDITION_FILTER, payload);
}

export const clearFilters = ({commit}) => {
  commit(types.CLEAR_FILTERS);
}



