import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as types from './mutation-types'

Vue.use(Vuex)

/*  */
export default new Vuex.Store({
  actions,
  state: {
    count:0,
    num:5,
    keywords:"",
    filters:{
      "osCategory":"",
      "osVersion":"",
      "location":"",
      "status":""
    },
    filterBol:true,
    device_list:[
      {
        "uuid": "001",
        "brand": "iOS",
        "phoneVersion": "iPhone 7",
        "osVersion": "10.0",
        "phone_img":"iphone7",
        "owner":"",
        "location":"",
        "state":"1"
      },
      {
        "uuid": "002",
        "brand": "Android",
        "phoneVersion": " P9 PLUS",
        "osVersion": "6.0",
        "phone_img":"p9plus",
        "location":"",
        "owner":"",
        "state":"2"
      },
      {
        "uuid": "003",
        "brand": "iOS",
        "phoneVersion": "iPhone 7 Plus",
        "osVersion": "10.0",
        "phone_img":"iphone7p",
        "location":"",
        "owner":"",
      },
      {
        "uuid": "004",
        "brand": "Android",
        "phoneVersion": "OPPO R9s Plus",
        "osVersion": "6.0",
        "phone_img":"r9sp",
        "location":"",
        "owner":"",
        "state":"1"
      },
      {
        "uuid": "05",
        "brand": "iOS",
        "phoneVersion": "iPhone 6s Plus",
        "osVersion": "9.0",
        "phone_img":"iphone6sp",
        "location":"",
        "owner":"",
        "state":"2"
      }
    ],
    phoneData:{
      "uuid": "05",
      "brand": "iOS",
      "phoneVersion": "iPhone 6s Plus",
      "osVersion": "9.0",
      "phone_img":"iphone6sp",
      "location":"",
      "owner":"",
      "state":"2"
    }
  },
  mutations: {
    [types.INCREMENT_COUNT](state,num){
      state.count+=num;
    },
    [types.CONDITION_FILTER](state,payload){
      state.filters[payload.condition]=payload.value;
      state.filterBol=true;
    },
    [types.CLEAR_FILTERS](state){
      "use strict";
      state.filterBol=false;
      let filters=state.filters;
      for(var key in filters){
        filters[key]="";
      }
    }
  }
})




